import Vue from 'vue'//Vue
import App from './App.vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

Vue.config.productionTip = false//
import goods from './components/content/goods.vue'
import ratings from './components/content/ratings.vue'
import seller from './components/content/seller.vue'
import all from './components/content/all.vue'
import good from './components/content/good.vue'
import bad from './components/content/bad.vue'
let router=new VueRouter({
	routes:[
		{
			path:'/',//goods
			redirect:'goods'
		},
		{
			path:"/goods",
			components:{
				viewA:goods,
			}
		},
		{
			path:'/ratings',
			components:{
				viewA:ratings
			},
			children:[//ratings()
				{
					path:'/',
					redirect:'/all'
				},
				{
					path:'/all',
					components:{
						viewB:all
					}
				},
				{
					path:'/good',
					components:{
						viewB:good
					},
				},
				{
				path:'/bad',
				components:{
					viewB:bad
				}
				}]
		},
		{
			path:'/seller',
			components:{
				viewA:seller
			},
		},
	]
})
new Vue({
	router,//
  render: h => h(App),//App(App.vue)el
}).$mount('#app')//Appidapp

